// import {Helmet} from 'react-helmet'
import React from 'react';
import Particles from 'react-particles-js';
import Tilt from 'react-tilt';
import {
  FaInstagram,
  FaFacebookF,
  FaTwitter,
  FaLinkedinIn,
  FaGithub,
  FaStackOverflow,
  FaTwitch,
  FaSteam
} from 'react-icons/fa';

import './Banner.scss';
import profileImage from './Img/profile.png';

const Banner = (props) => {

  //useEffect
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);


  return (
    <React.Fragment>
      <section id="Banner">

        <Particles
          style={{ background: '#000' }}
          params={{
            "particles": {
              "number": {
                "value": 70
              },
              "size": {
                "value": 3
              }
            },
            "interactivity": {
              "events": {
                "onhover": {
                  "enable": true,
                  "mode": "repulse"
                }
              }
            }
          }} />

        <div className="profile-block">

          <div className="img-wrap">
            <Tilt options={{ max: 20 }} >
              <img src={profileImage} alt="profile headshot" />
            </Tilt>
          </div>

          <div className="text-block">
            <h2>Habibullah</h2>
            <p> Full-Stack Developer</p>
            <a href="https://github.com/aharrfaris" target="_blank" rel="noopener noreferrer">github.com/aharrfaris</a>

            <ul>

              <li>
                <a href="https://github.com/aharrfaris" target="_blank" rel="noopener noreferrer">
                  <FaGithub />
                </a>
              </li>

              <li>
                <a href="https://www.linkedin.com/in/aharrfaris" target="_blank" rel="noopener noreferrer">
                  <FaLinkedinIn />
                </a>
              </li>

              <li>
                <a href="https://stackoverflow.com/users/9958708/aharrfaris" target="_blank" rel="noopener noreferrer">
                  <FaStackOverflow />
                </a>
              </li>

              <li>
                <a href="https://twitter.com/AharrFaris" target="_blank" rel="noopener noreferrer">
                  <FaTwitter />
                </a>
              </li>

              <li>
                <a href="https://www.facebook.com/AharrFaris" target="_blank" rel="noopener noreferrer">
                  <FaFacebookF />
                </a>
              </li>

              <li>
                <a href="https://www.instagram.com/aharrfaris" target="_blank" rel="noopener noreferrer">
                  <FaInstagram />
                </a>
              </li>

              <li>
                <a href="https://steamcommunity.com/id/AharrFaris" target="_blank" rel="noopener noreferrer">
                  <FaSteam />
                </a>
              </li>

              <li>
                <a href="https://www.twitch.tv/aharrfaris" target="_blank" rel="noopener noreferrer">
                  <FaTwitch />
                </a>
              </li>

            </ul>
          </div>

        </div>

      </section>
    </React.Fragment>
  );
};

export default Banner;