// import {Helmet} from 'react-helmet'
import React from 'react';
import { NavLink } from 'react-router-dom';
import Sticky from 'react-sticky-el';

import './Navs.scss';

const Navs = (props) => {


  return (
    <Sticky stickyClassName="sticky-navs">
      <nav id="Navs">
        <NavLink exact className="nav-link" to="/">MySelf</NavLink>
        <NavLink exact className="nav-link" to="/projects">Projects</NavLink>
        <NavLink exact className="nav-link" to="/contact">Contact</NavLink>
      </nav>
    </Sticky>
  );
};

export default Navs;