// import {Helmet} from 'react-helmet'
import React from 'react';

import {
  Switch,
  Route,
  Redirect
} from 'react-router-dom';

import { Spinner } from 'reactstrap';

import './Main.scss';

import Navs from '../UI/Nav/Nav';
import Banner from './Banner/Banner';
const MySelf = React.lazy(() => import('./Routes/MySelf/MySelf'));
const Projects = React.lazy(() => import('./Routes/Projects/Projects'));
const Contact = React.lazy(() => import('./Routes/Contact/Contact'));

const Main = (props) => {

  // useEffect
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <React.Fragment>
      <section id="Main">
        <Banner />
        <Navs />

        {/* <Spinner color="danger" className="router-spinner" /> */}

        <Switch>
          <Route
            exact
            path="/"
            render={() => (
              <React.Suspense fallback={<Spinner color="danger" className="router-spinner" />}>
                <MySelf />
              </React.Suspense>
            )}
          />
          <Route
            exact
            path="/projects"
            render={() => (
              <React.Suspense fallback={<Spinner color="danger" className="router-spinner" />}>
                <Projects />
              </React.Suspense>
            )}
          />
          <Route
            exact
            path="/contact"
            render={() => (
              <React.Suspense fallback={<Spinner color="danger" className="router-spinner" />}>
                <Contact />
              </React.Suspense>
            )}
          />
          <Redirect from="*" to="/" />
        </Switch>
      </section>
    </React.Fragment >
  );
};

export default Main;;;